import React from 'react'
import {connect} from 'react-redux'
import {signout} from '../actions/auth'

class Header extends React.Component{
    handleSignin() {
        if (this.props.authenticated) {
            this.props.signout()
            location.href = '/'
        } else {
            location.href = '/signin'
        }
    }
    onSearch() {
        alert('Waiting for backend developer~')
    }
    render() {
        return (
            <div>
                <nav className="navbar navbar-expand-md navbar-light bg-light fixed-top">
                    <a className="navbar-brand" href="/">Logiwork CMS</a>
                    <button className="navbar-toggler" type="button" data-toggle="collapse"
                            data-target="#navbarCollapse" aria-controls="navbarCollapse" aria-expanded="false"
                            aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarCollapse">
                        <ul className="navbar-nav mr-auto">
                            <li className="nav-item active">
                                <a className="nav-link" href="/">Home </a>
                            </li>
                            <li className="nav-item dropdown active">
                                <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button"
                                   data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                    Translation
                                </a>
                                <div className="dropdown-menu" aria-labelledby="navbarDropdown">
                                    <a className="dropdown-item" href="/projectDetails">Project Details</a>
                                    <div className="dropdown-divider"></div>
                                    <a className="dropdown-item" href="/portfolios">Portfolios</a>
                                    <div className="dropdown-divider"></div>
                                    <a className="dropdown-item" href="/messages">Messages</a>
                                </div>
                            </li>
                        </ul>

                        <button className="btn btn-outline-success my-2 mx-4 my-sm-0" style={{width: '100px'}} type="submit" onClick={this.handleSignin.bind(this)}>{this.props.authenticated ? 'Sign out': 'Sign in'}
                        </button>
                    </div>
                </nav>
            </div>
        )
    }
}
function mapStateToProps(state){
    return{
        authenticated:state.auth.authenticated
    }
}
export default connect(mapStateToProps, {signout})(Header)





