import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { createStore, applyMiddleware, compose } from 'redux';
import reduxThunk from 'redux-thunk';
import reducers from './reducers';

import { persistStore, persistReducer } from 'redux-persist'
import storage from 'redux-persist/lib/storage'
import { PersistGate } from 'redux-persist/integration/react'

import Immutable from "immutable";

import installDevTools from "immutable-devtools";
installDevTools(Immutable);

const persistConfig = {
    key: 'root',
    whitelist: ['auth'],
    storage,
}

const persistedReducer = persistReducer(persistConfig, reducers)

import {
    BrowserRouter as Router,
    Route
} from 'react-router-dom'

import HomePage from './components/homePage.js'
import Signin from './components/signin.js'
import ProjectDetailsContainer from './components/projectDetailsContainer.js'
import ProjectDetailShow from './components/projectDetail_show.js'
import PortfoliosContainer from './components/portfoliosContainer.js'
import PortfolioShow from './components/portfolio_show.js'
import MessagesContainer from './components/messagesContainer.js'

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(persistedReducer, /* preloadedState, */ composeEnhancers(applyMiddleware(reduxThunk)))
let persistor = persistStore(store)

ReactDOM.render(
    <Provider store={store}>
        <PersistGate loading={null} persistor={persistor}>
        <Router>
            <div className='flexGrow d-flex flex-column'>
                <Route exact path='/' component={HomePage} />
                <Route path='/signin' component={Signin} />
                <Route exact path='/projectDetails' component={ProjectDetailsContainer} />
                <Route path='/projectDetails/:id' component={ProjectDetailShow} />
                <Route exact path='/portfolios' component={PortfoliosContainer} />
                <Route path='/portfolios/:id' component={PortfolioShow} />
                <Route path='/messages' component={MessagesContainer} />
            </div>
        </Router>
        </PersistGate>
    </Provider>
    ,  document.getElementById("app"));



